import UrlParser from '../../routes/url-parser';
import TheRestaurantDbSource from '../../data/therestaurantdb-source';
import { createRestaurantDetailTemplate } from '../templates/template-creator';
import LikeButtonInitiator from '../../utils/like-button-initiator';
import API_ENDPOINT from '../../globals/api-endpoint';

const Detail = {
  async render() {
    return `
      <div id="restaurant" class="restaurant"></div>
      <div id="likeButtonContainer"></div>
    `;
  },
  
  async afterRender() {
    const url = UrlParser.parseActiveUrlWithoutCombiner();
    const restaurant = await TheRestaurantDbSource.detailRestaurant(url.id);
    const restaurantContainer = document.querySelector('#restaurant');
    restaurantContainer.innerHTML = createRestaurantDetailTemplate(restaurant);
    
    const submitForm = document.getElementById('form-review');
    submitForm.addEventListener('submit', function (event) {
      event.preventDefault();
      const dataInput = {
        "id": url.id,
        "name": document.querySelector('#name-input').value,
        "review": document.querySelector('#review-input').value
      };

      const response = TheRestaurantDbSource.addRestaurantReview(API_ENDPOINT.ADD_REVIEW, dataInput);
      response.then((data) => {
        alert(data.message);
        document.querySelector('#name-input').value = '';
        document.querySelector('#review-input').value = '';
      }).catch((error) => {
        alert('Gagal menambahkan review ! karena', error);
      });
    });

    LikeButtonInitiator.init({
      likeButtonContainer: document.querySelector('#likeButtonContainer'),
      restaurant: {
        id: restaurant.restaurant.id,
        name: restaurant.restaurant.name,
        description: restaurant.restaurant.description,
        pictureId: restaurant.restaurant.pictureId,
        rating: restaurant.restaurant.rating
      },
    });
  },
};

export default Detail;
