import DrawerInitiator from '../utils/drawer-initiator';
import UrlParser from '../routes/url-parser';
import routes from '../routes/routes';
import FavoriteRestaurantIdb from '../../scripts/data/therestaurantdb-source';

class App {
  constructor({ button, drawer, content, hero, nav_menu }) {
    this._button = button;
    this._drawer = drawer;
    this._content = content;
    this._hero = hero;
    this._nav_menu = nav_menu;

    this._initialAppShell();
  }

  _initialAppShell() {
    DrawerInitiator.init({
      button: this._button,
      drawer: this._drawer,
      content: this._content,
      hero: this._hero,
      nav_menu: this._nav_menu
    });
  }

  async renderPage() {
    document.querySelector('.lds-ring').style.display = 'block';
    const url = UrlParser.parseActiveUrlWithCombiner();
    const page = routes[url];
    this._content.innerHTML = await page.render();
    await page.afterRender();

    const skipLinkElem = document.querySelector('.skip-link');    
    skipLinkElem.addEventListener('click', (event) => {      
      event.preventDefault();
      document.querySelector('#mainContent').focus();
    });

    if (url == '/' || url == '/daftar-restoran') {
      document.querySelector('#beranda').style.color = '#E55604';
      document.querySelector('#favorite').style.color = 'black';
    } else if (url == "/like") {
      document.querySelector('#favorite').style.color = '#E55604';
      document.querySelector('#beranda').style.color = 'black';
    } else {
      document.querySelector('#beranda').style.color = 'black';
      document.querySelector('#favorite').style.color = 'black';
    }
  }
}

export default App;
