import CONFIG from '../../globals/config';
import 'lazysizes';
import 'lazysizes/plugins/parent-fit/ls.parent-fit';

const createRestaurantDetailTemplate = (restaurantDetail) => {
  const dataFoods = restaurantDetail.restaurant.menus.foods.map(food => {
    return `<li>${food.name}</li>`;
  });
  const dataDrinks = restaurantDetail.restaurant.menus.drinks.map(drinks => {
    return `<li>${drinks.name}</li>`;
  });
  const dataReviews = restaurantDetail.restaurant.customerReviews.map(review => {
    return `
      <div class="reviews">
        <p class="comment">"${review.review}"</p>
        <p class="date">${review.date}</p>
        <p class="name">${review.name}</p>
      </div>
    `;
  });
  
  return `
    <img class="restaurant__poster" src="${CONFIG.BASE_IMAGE_URL + restaurantDetail.restaurant.pictureId}" alt="${restaurantDetail.restaurant.name}" />
    <h2 class="restaurant__title">${restaurantDetail.restaurant.name}</h2>
    <div class="restaurant__info">
      <h3>Informasi Restoran</h3>
      <h4>Kota</h4>
      <p>${restaurantDetail.restaurant.city}</p>
      <h4 class="address">Alamat</h4>
      <p>${restaurantDetail.restaurant.address}</p>
      <h4>Menu Makanan</h4>
      <ul class="menu foods">
        ${dataFoods.join(" ")}
      </ul>
      <h4>Menu Minuman</h4>
      <ul class="menu drinks">
        ${dataDrinks.join(" ")}
      </ul>
      <div class="restaurant__overview">
        <h4>Deskripsi</h4>
        <p>${restaurantDetail.restaurant.description}</p>
      </div>
      <h4 class="title-reviews">Customer Reviews</h4>
      <div class="container-reviews">
        ${dataReviews.join(" ")}
      </div>
      <form class="form-review" action="#" id="form-review">
        <h4 class="add-review">Tulis Review</h4>
        <div class="form-group form-title">
          <label for="name-input">Nama</label>
          <input type="text" id="name-input" name="name-input" placeholder="Isi nama anda..." required>
        </div>
        <div class="form-group form-title">
          <label for="review-input">Komentar</label>
          <textarea id="review-input" name="review-input" rows="3" placeholder="Isi komentar anda..." required></textarea>
        </div>
        <input type="submit" value="Submit" name="Submit" class="btn-submit">
      </form>
    </div>
  `;
}

  const createRestaurantItemTemplate = (restaurant) => `
    <a href="/#/detail/${restaurant.id}" class="restaurant-item">
      <div class="restaurant-item__header">
        <img class="restaurant-item__header__poster lazyload" alt="${restaurant.name}"
            src="${CONFIG.BASE_IMAGE_URL + restaurant.pictureId}">
        <div class="restaurant-item__header__rating">
          <p>⭐️<span class="restaurant-item__header__rating__score">${restaurant.rating}</span></p>
        </div>
      </div>
      <div class="restaurant-item__content">
        <h3 class="restaurant-name">${restaurant.name}</h3>
        <p>${restaurant.description}</p>
      </div>
    </a>
  `;

  const createLikeButtonTemplate = () => `
    <button aria-label="like this restaurant" id="likeButton" class="like">
      <i class="fa fa-thumbs-up" aria-hidden="true"></i>
    </button>
  `;
const createLikedButtonTemplate = () => `
  <button aria-label="unlike this restaurant" id="unLikeButton" class="like">
    <i class="fa fa-trash" aria-hidden="true"></i>
  </button>
`;

export {
  createRestaurantItemTemplate,
  createRestaurantDetailTemplate,
  createLikeButtonTemplate,
  createLikedButtonTemplate,
};
